class App extends React.Component {
  //only setSate call -> new state and render function call 
  /*
  constructor(props){
    super(props);
  }
  */
  state = {
    //count : 0
    //
    isLoading : true,
    movies : []
  };

  add = () => {
    this.setState(current => ({count : current.count+1}));
  };

  minus = () => {
   this.setState(current => ({count : this.state.count-1})); 
  };

  getMovies = async() => {
    const {
      data : { 
        data   : {movies}
      } 
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");

    this.setState({movies, isLoading:false})
    
    console.log(movies);
  };

  componentDidMount(){
    // setTimeout(() => { 
    //   this.setState({isLoading : false }); 
    // },6000);
    this.getMovies();
  };

  componentDidUpdate(){};
  componentWillUnmount(){};
 
  /* 
    state changed(by setState) -> render function call, directly state changed -> render function not call 
  */
  render(){
    const {isLoading , movies} =  this.state;
    
    console.log(movies);

    return <section className="container"> 
            { isLoading ? 
             (<div className="Loader"><span className="Loader_text">"Loading..."</span></div>) 
             :
             (
              
              <div className="movies"> 
              { 
                
                movies.map(movie => (
                <Movie key = {movie.id} 
                  id = {movie.id} 
                  year = {movie.year} 
                  title = {movie.title} 
                  summary = {movie.summary} 
                  poster = {movie.medium_cover_image} 
                  genres = {movie.genres}
                />
                ))
              }
              </div>
             )
            }
           </section>
      // <div>
      //   <h1>....The number is {this.state.count}</h1>
      //   <button onClick = {this.add}>add</button>
      //   <button onClick = {this.minus}>minus</button>
      // </div>
  }  
}

export default App;
